import Vue from 'vue'
import Router from 'vue-router'
import MapSearch from '@/components/MapSearch'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/map',
      name: 'map',
      component: MapSearch
    }
  ]
})



