<template>
  <div class="container">
    <div id="map" class="map" v-on:click="toggle">
    </div>
    <div class="gallery">
      <mt-popup v-model="popupVisible" position="bottom" class="mint-popup" :modal=false>
        <mt-button size="small" id="btnfilter" v-on:click="showfilter" class="filter">筛选条件
          <!--<img src="../assets/close_btn_img.png" height="12px" width="12px">-->
        </mt-button>
        <ul class="list-house"
            v-loading="loading"
            element-loading-text="加载中..."
            element-loading-spinner="el-icon-loading">
          <li class="item-houe" v-for="(house, index) in houses" v-bind:key="index">
            <a v-bind:href='house.url' style="text-decoration: none">
              <img v-bind:src='house.img' class="img-house" alt="房源图片"/>
              <div class="desc-house">
                <p class="address">{{house.address}}</p>
                <p class="configure">{{house.bedroom}}室{{house.toilet}}卫&nbsp;&nbsp;{{house.areas}}</p>
                <p class="type">{{house.housetype}}</p>
                <p class="price">{{house.price}}</p>
              </div>
            </a>
          </li>
        </ul>
      </mt-popup>
    </div>
   <div>
     <mt-popup v-model="filterPopupVisible" position="top" class="filter-popup">
       <div>
         <mt-header title="筛选条件">
           <router-link to="/map" slot="left">
             <mt-button icon="back" v-on:click="close">返回</mt-button>
           </router-link>
         </mt-header>
        <div class="select">
          <span class="selectdec">选择国家：</span>
          <el-dropdown placement="bottom-start" @command="handleCountry" trigger="click">
            <el-button type="default" class="selectdecin">
              {{countryname}}<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown" style="width: 48%">
              <el-dropdown-item v-for="(country, index) in countries" v-bind:key="index" :command="index">{{country.countryname}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
         <div class="select">
           <span class="selectdec">选择省份：</span>
           <el-dropdown placement="bottom-start" @command="handleProv" trigger="click">
             <el-button type="default" class="selectdecin">
               {{provname}}<i class="el-icon-arrow-down el-icon--right"></i>
             </el-button>
             <el-dropdown-menu slot="dropdown" style="width: 48%">
               <el-dropdown-item v-for="(prov, index) in provs" v-bind:key="index" :command="index">{{prov.provname}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
         <div class="select">
           <span class="selectdec">选择大区：</span>
           <el-dropdown placement="bottom-start"  @command="handleGroup" trigger="click">
             <el-button type="default" class="selectdecin">
               {{groupname}}<i class="el-icon-arrow-down el-icon--right"></i>
             </el-button>
             <el-dropdown-menu slot="dropdown" style="width: 48%">
               <el-dropdown-item v-for="(group, index) in groups" v-bind:key="index" :command="index">{{group.groupname}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
         <div class="select">
           <span class="selectdec">选择城市：</span>
           <el-dropdown placement="bottom-start" @command="handleCity" trigger="click">
             <el-button type="default" class="selectdecin">
               {{cityname}}<i class="el-icon-arrow-down el-icon--right"></i>
             </el-button>
             <el-dropdown-menu slot="dropdown" style="width: 48%">
               <el-dropdown-item v-for="(city, index) in cities" v-bind:key="index" :command="index">{{city.cityname}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
         <div class="select">
           <span class="selectdec">房源类型：</span>
           <el-dropdown placement="bottom-start" @command="handleHouseType" trigger="click">
             <el-button type="default" class="selectdecin">
               {{housetypeValue}}<i class="el-icon-arrow-down el-icon--right"></i>
             </el-button>
             <el-dropdown-menu slot="dropdown" style="width: 48%">
               <el-dropdown-item v-for="(type, index) in types" v-bind:key="index" :command="index">{{type.name}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
       </div>
       <div class="select">
         <span class="selectdec">开始时间：</span>
         <el-date-picker
           :editable="false"
           v-model="starttime"
           type="datetime"
           size="small"
           value-format="yyyyMMddHHmmss"
           @change="(value) => changeStartTime(value)"
           placeholder="选择开始日期与时间">
         </el-date-picker>
       </div>
       <div class="select">
         <span class="selectdec">结束时间：</span>
         <el-date-picker
           :editable="false"
           v-model="endtime"
           @change="(value) => changeEndTime(value)"
           size="small"
           type="datetime"
           value-format="yyyyMMddHHmmss"
           placeholder="选择结束日期与时间">
         </el-date-picker>
       </div>
       <mt-button type="primary" size="large" class="btnconfirm" v-on:click="selectCompleted">确定</mt-button>
     </mt-popup>
   </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      popupVisible: false,
      filterPopupVisible: false,
      baiduurl: '',
      /* 所有要显示的房源 */
      houses: [],
      /* 国家 */
      countries: [],
      provs: [],
      groups: [],
      /* 城市 */
      cities: [],
      types: [{'name': '在售', 'value': 'saling'}, {'name': '已售', 'value': 'saled'}, {'name': '租赁', 'value': 'rent'}],
      /* 选中国家 */
      country: '',
      /* 筛选省份 */
      prov: '',
      /* 选中group */
      group: '',
      /* 选中城市 */
      city: '',
      opendate: '',
      housetypeValue: '房源类型',
      housetype: '',
      starttime: '',
      endtime: '',
      countryname: '选择国家',
      provname: '选择省份',
      groupname: '选择大区',
      cityname: '选择城市',
      map: {},
      marker: {},
      center: {lat: 49.2827291, lng: -123.1207375},
      zoom: 12,
      loading: true
    }
  },
  created () {
    var countryurl = 'http://www.realtoraccess.com/web/get/countries/'
    axios.get(countryurl).then((response) => {
      this.countries = response.data
      // console.log('======>>>>>>>' + response.data[0].countryname)
    })
  },
  mounted () {
    this.map = new google.maps.Map(document.getElementById('map'), {
      zoom: this.zoom,
      center: this.center
    })
    var url = 'http://www.realtoraccess.com/web/zone/listings/'
    axios.get(url).then((response) => {
      this.houses = response.data
      this.houses.forEach(this.generateMarker)
      this.loading = false
    })
  },
  methods: {
    /* 根据经纬度添加标记 */
    generateMarker: function (house, index) {
      var lat = house.lat
      var lng = house.lng
      if (lat === '' || lng === '') {
        new google.maps.Geocoder().geocode({
          'address': house.address
        }, function (results, status) {
          if (status === 'OK') {
            lat = results[0].geometry.location.lat()
            lng = results[0].geometry.location.lng()
            // console.log('lat: ' + lat + '<=>lng: ' + lng)
            this.createdMarker(lat, lng, house.price)
          }
        }.bind(this))
      } else {
        this.createdMarker(lat, lng, house.price)
      }
    },
    /* 有经纬度的直接添加到地图上 */
    createdMarker: function (lat, lng, price) {
      new google.maps.Marker({
        icon: 'http://www.realtoraccess.com/static/web/images/label.png',
        label: {
          fontFamily: 'STKaiti',
          fontSize: '16px',
          color: '#EA7865',
          fontWeight: 'normal',
          text: price
        },
        position: {lat: Number(lat), lng: Number(lng)},
        map: this.map,
        animation: google.maps.Animation.DROP
      })
    },
    handleClick: function () {
      this.popupVisible = true
    },
    /* 切换房源列表是否显示 */
    toggle: function () {
      if (this.popupVisible) {
        this.popupVisible = false
      } else {
        this.popupVisible = true
      }
    },
    /* 显示筛选条件页面 */
    showfilter: function () {
      if (this.filterPopupVisible) {
        this.filterPopupVisible = false
      } else {
        this.filterPopupVisible = true
      }
    },
    /* 筛选条件选择完成 */
    selectCompleted: function () {
      // 关闭页面
      this.filterPopupVisible = false
      this.opendate = this.startTime + '-' + this.endTime
      // console.log('opendate======>>>>' + this.opendate)
      this.loading = true
      this.getFilterHouse()
    },
    close: function () {
      this.filterPopupVisible = false
    },
    handleCountry: function (index) {
      this.countryname = this.countries[index].countryname
      this.country = this.countries[index].countryid
      this.getprov(this.countries[index].countryid)
    },
    handleProv: function (index) {
      this.provname = this.provs[index].provname
      this.prov = this.provs[index].provid
      this.getgroup(this.provs[index].provid)
    },
    handleGroup: function (index) {
      this.groupname = this.groups[index].groupname
      this.group = this.groups[index].groupid
      this.getcity(this.groups[index].groupid)
    },
    handleCity: function (index) {
      this.cityname = this.cities[index].cityname
      this.city = this.cities[index].cityid
    },
    handleHouseType: function (index) {
      this.housetypeValue = this.types[index].name
      this.housetype = this.types[index].value
      // console.log('housetype======>>>>' + this.housetype)
    },
    getprov: function (country) {
      axios.get('http://www.realtoraccess.com/web/get/provs/', {params: { 'countryid': country }}).then((response) => {
        this.provs = response.data
        // console.log('prov======>>>>' + response.data)
      })
    },
    getgroup: function (provid) {
      axios.get('http://www.realtoraccess.com/web/get/groups/', {params: {'provid': provid}}).then((response) => {
        this.groups = response.data
        // console.log('group======>>>>' + response.data)
      })
    },
    getcity: function (groupid) {
      axios.get('http://www.realtoraccess.com/web/get/cities/', {params: {'groupid': groupid}}).then((response) => {
        this.cities = response.data
        // console.log('city======>>>>' + response.data)
      })
    },
    getFilterHouse: function () {
      axios.get('http://www.realtoraccess.com/web/zone/listings/', {params: {'country': this.country, 'prov': this.prov, 'group': this.group, 'city': this.city, 'opendate': this.opendate, 'housetype': this.housetype}}).then((response) => {
        this.houses = response.data
        this.loading = false
      })
    },
    changeStartTime: function (value) {
      this.startTime = value
      // console.log('startTime======>>>>' + value)
    },
    changeEndTime: function (value) {
      this.endTime = value
      // console.log('endTime======>>>>' + value)
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  .container {
    overflow: hidden;
    height: 100%;
  }
  .map {
    margin: auto;
    height: 100%;
  }
  .gallery {
    background-color: #4caf50;
    height: 45%;
    overflow-x: auto;
    overflow-y: hidden;
    padding: 0;
  }
  a:hover {
    font-color:yellow
  }
  .list-house{
    width:100%;
    height: 100%;
    display: inline;
    white-space: nowrap;
    overflow-x:scroll;
    float:left;
    overflow-y:hidden
  }
  .list-house li{
    display:inline-block;
    width:55%;
    height:100%;
  }
  .item-houe{
    margin-left: 15px;
  }
  .img-house{
    border-radius: 5px;
    margin-top: 10px;
    background-color: #E0E0E0;
    width: 100%;
    height: 60%;
  }
  .desc-house{
    text-align: left;
    height: 30%;
  }
  .address{
    color: black;
    font-size: 14px;
    font-weight: bold;
  }
  .configure{
    font-size: 13px;
    color: #1B1B23;
    padding-bottom: 3px;
  }
  .type{
    font-size: 13px;
    color: #1B1B23;
    padding-bottom: 3px;
  }
  .price{
    color: #EA7865;
    font-size: 16px;
  }
  .mint-popup {
    width: 100%;
    height: 40%;
    text-align: center;
  }
  .filter-popup {
    padding-left: 10px;
    padding-right: 10px;
    text-align: left;
    width: 100%;
    height: 100%;
  }
  .filter{
    background-color: white;
    padding-right: 20px;
    padding-left: 20px;
    border-radius: 25px;
    transform:translate(-50%,-50%);
    position : absolute;
    bottom: 100%;
  }
  .btnconfirm{
    left: 10%;
    width: 80%;
    position : absolute;
    bottom: 10px;
  }
  .filter_desc{
    font-size: 15px;
    color: #6C6C7A;
  }
  .select{
    width: 100%;
    margin-left: 12px;
    margin-top: 12px;
  }
  .selectdec{
    font-size: 16px;
    color: #6C6C7A;
  }
  .selectdecin{
    width:200px;
    font-size: 14px;
    color: #6C6C7A;
  }
  iframe{
    border: white;
    width: 100%;
    height: 100%;
  }
</style>



